<template>
	<div class="container">
		<div class="row">
			<div class="one-half column" style="margin-top: 5%">
				<h1>Pedido #{{ entity_id }}</h1>
			</div>
		</div>
		<div class="row">
			<div class=" column" style="margin-top: 5%">
				<div>
					<cliente ref="cliente"/>
						<items :items="items" />
						<div class="row">
							<textarea disabled v-model="obs" class="u-full-width" placeholder="Observações.."></textarea>
						</div>
						<div>
							<p>Total: {{ total | moneyFormat }} </p>
						</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import Cliente from './parts/Cliente.vue'
import Items from './parts/Items.vue'
import helper from '../../data/helper.js'
import api from '../../data/api.js'

export default {
	name: 'pedidosView',
	data(){
		return{
			items: [],
			total: 0,
			obs: '',
			cont: 0
		}
	},
	props: {
		entity_id: String
	},
	components: {
		Cliente,
		Items
	},
	filters: {
		moneyFormat(val){
			return helper.moneyFormat(val)
		},
	},
	methods: {
		toPage(e){
			this.$parent.toPage(e)
		},
		getItems(ids){
			var data = ids.split(',')
			var items = []
			data.forEach(function(item){
				if(item !== ''){
					var id = item.split('|')[0]
					var qty = item.split('|')[1]
					items.push({id: id, qty: qty})
				}
			})
			items.forEach(function(item){
				var params = {
					from: 'produtos',
					action: 'find',
					where: `id|${item.id}`
				}

				Promise.resolve(api.getEntity(params))
					.then(function (results) {
						results.data.msg[0].qty = item.qty
						results.data.msg[0].total = parseInt(item.qty) * helper.numberFormat(results.data.msg[0].value)
						results.data.msg[0].total = helper.moneyFormat(results.data.msg[0].total)
						items.pop(item)
						items.push(results.data.msg[0])
					});
			})
			return items
		}
	},
	mounted() {
		var params = {
			from: 'pedidos',
			action: 'find',
			where: `id|${this.entity_id}`
		}

		const self = this;
		Promise.resolve(api.getEntity(params))
			.then(function (results) {
				self.$refs.cliente.clienteName = results.data.msg[0].cliente_name
				self.total = results.data.msg[0].total
				self.obs = results.data.msg[0].obs
				self.items = self.getItems(results.data.msg[0].prod_qty)
			});
	}
}
</script>

