import axios from 'axios'

var api = {
	url: 'http://35.202.167.228/back/api/index.php',
	//	url: 'http://localhost/eat-fit/back/api/index.php',
	get: function (params) {
		axios.get(`${this.url}`, {
			params: params
		})
			.then(function (response) {
				console.log(response);
			})
			.catch(function (error) {
				console.log(error);
			})
	},

	getEntityList: async function (params) {
		try {
			const response = await axios.get(`${this.url}`, {params: params})
			return response
		} catch (error) {
			console.error(error);
		}
	},

	getEntityCompleteList: async function (params) {
		try {
			const response = await axios.get(`${this.url}`, {params: params})
			return response
		} catch (error) {
			console.error(error);
		}
	},

	cleanEntity: async function (params) {
		try {
			const response = await axios.get(`${this.url}`, {params: params})
			return response
		} catch (error) {
			console.error(error);
		}
	},

	getEntity: async function (params) {
		try {
			const response = await axios.get(`${this.url}`, {params: params})
			return response
		} catch (error) {
			console.error(error);
		}
	},

	saveEntity: async function (params) {
		try {
			const response = await axios.post(`${this.url}`, params, {
				headers: {
					'Content-Type': 'application/json',
				}
			})
			return response
		} catch (error) {
			console.error(error);
		}
	}
}


export default api
