<template>
	<div class="container">
		<div class="row">
			<div class="column" >
				<h4>Nova Saída</h4>
			</div>
		</div>
		<form @submit.prevent="processForm($event)" id="saidas_form">
			<div v-for="error in errors" :key="error.field">
				<p class="error">**{{ error.msg }}</p>
			</div>
			<div class="row">
				<div class=" columns">
					<label for="description">Descrição</label>
					<textarea v-model="description" class="u-full-width" placeholder="Descrição da saída..." id="description"></textarea>
				</div>
			</div>
			<div class="row">
				<div class="six columns">
					<label for="value">Valor</label>
					<input hidden v-model="value" class="u-full-width" type="text" id="value">
					<input  v-model="moneyFormat"
							@blur="focusOut" @focus="focusIn" @change="updateValue"
							class="u-full-width" type="text" placeholder="R$0,00" id="moneyFormat">
				</div>
			</div>
			<input class="button" type="button" value="Limpar">
			<input id="btn-save" class="button-primary" type="submit" value="Salvar">
		</form>
	</div>
</template>

<script>
import api from '../../data/api'
import helper from '../../data/helper'

export default {
	name: 'saidasNew',
	data() {
		return {
			moneyFormat: 'R$ 0,00',
			description: '',
			value: '',
			errors: []
		}
	},
	methods: {
		toPage(e){
			this.$parent.toPage(e)
		},
		focusIn(e){
			this.moneyFormat = helper.moneyFormat(e.target.value)
		},
		focusOut(e){
			this.moneyFormat = helper.moneyFormat(e.target.value)
		},
		updateValue(e){
			this.value = helper.numberFormat(e.target.value)
		},
		processForm(){
			this.errors = []
			var params = {
				action: 'create',
				from: 'saidas',
				description: {value: this.description, rule: 'string'},
				value: {value: this.value, rule: 'number'}
			}
			params = helper.chckeEmpytFields(params)
			if(params.error){
				this.errors = params.error
				return
			}
			const self = this;
			Promise.resolve(api.saveEntity(params))
				.then(function (results) {
					const resp = results;
					if(resp.data.msg == true){
						alert("saída cadastrada! :)")
						self.toPage('saidasList')
					}
				});
		}
	},
	mounted(){
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.error{
	color: red;
}
</style>
