<template>
	<div style="margin-top: 3%">
		<div class="container">
			<div class="row">
				<div class="two columns">
					<h4>{{msg}}</h4>
				</div>
				<div class="ten columns">
					<button style="margin-right: 1%" @click="toPage('pedidosList')" class="button">Pedidos</button>
					<button style="margin-right: 1%" @click="toPage('produtosList')" class="button">Produtos</button>
					<button style="margin-right: 1%" @click="toPage('caixaView')" class="button">Caixa</button>
					<button style="margin-right: 1%" @click="toPage('clientesList')" class="button">Clientes</button>
					<button style="margin-right: 1%" @click="toPage('saidasList')" class="button">Saídas</button>
				</div>
			</div>
		</div>
		<hr>
		<component v-bind:is="component" :entity_id="entity_id" />
	</div>
</template>

<script>
import pedidosList from './pedidos/LIst.vue'
import pedidosView from './pedidos/View.vue'
import pedidosNew from './pedidos/New.vue'
import produtosList from './produtos/LIst.vue'
import produtosView from './produtos/View.vue'
import produtosNew from './produtos/New.vue'
import caixaView from './caixa/View.vue'
import clientesList from './clientes/LIst.vue'
import clientesView from './clientes/View.vue'
import clientesNew from './clientes/New.vue'
import saidasList from './saidas/LIst.vue'
import saidasNew from './saidas/New.vue'
import saidasView from './saidas/View.vue'

export default {
	name: 'Main',
	components: {
		pedidosView,
		pedidosList,
		pedidosNew,
		produtosNew,
		produtosList,
		produtosView,
		clientesList,
		clientesNew,
		clientesView,
		saidasList,
		saidasView,
		saidasNew,
		caixaView
	},
	props: {
		msg: String
	},
	data() {
		return {
			entity_id: null,
			component: 'pedidosNew'
		}
	},
	methods: {
		toPage(page, id){
			if(id !== null)
				this.entity_id = id

			this.component = page
		}
	}
}
</script>

