<template>
	<div class="container">
		<div class="row">
			<div class="one-half column" style="margin-top: 5%">
				<h4>Novo Pedido</h4>
			</div>
		</div>
		<div class="row">
			<div class=" column" style="margin-top: 5%">
				<form @submit.prevent="processForm($event)">
					<div v-for="error in errors" :key="error.field">
						<p class="error">**{{ error.msg }}</p>
					</div>
					<cliente ref="cliente"/>
					<add-produto v-on:additem="addItem($event)" />
						<items :items="items" />
						<div class="row">
							<textarea v-model="obs" class="u-full-width" placeholder="Observações.."></textarea>
						</div>

						<div>
							<p @click="test()">Total: {{ total | moneyFormat }} </p>
						</div>
						<button type="submit">Salvar</button>
				</form>
			</div>
		</div>
	</div>
</template>

<script>
import Cliente from './parts/Cliente.vue'
import AddProduto from './parts/AddProduto.vue'
import Items from './parts/Items.vue'
import helper from '../../data/helper.js'
import api from '../../data/api.js'

export default {
	name: 'pedidosNew',
	data(){
		return{
			errors: [],
			items: [],
			total: 0,
			obs: '',
			cont: 0
		}
	},
	components: {
		Cliente,
		AddProduto,
		Items
	},
	filters: {
		moneyFormat(val){
			return helper.moneyFormat(val)
		},
	},
	methods: {
		processForm(){
			this.errors = []
			var prodQty = ''
			this.items.forEach(function(item){
				prodQty += `${item.id}|${item.qty},`
			})
			var params = {
				action: 'create',
				from: 'pedidos',
				obs: this.obs,
				cliente_name: {value: this.$refs.cliente.clienteName, rule: 'string'},
				total: {value: this.total, rule: 'number'},
				prod_qty: {value: prodQty, rule: 'string'},
			}
			params = helper.chckeEmpytFields(params)
			if(params.error){
				this.errors = params.error
				return
			}
			const self = this;
			Promise.resolve(api.saveEntity(params))
				.then(function (results) {
					const resp = results;
					if(resp.data.msg == true){
						const ok = true;
					}
				});
			this.stock(prodQty)
		},
		test(){
			var textToPrint = this.$refs.cliente.clienteName

			this.items.forEach(function(e){
				textToPrint +="\n-------------"
				textToPrint += '\nproduto:'+ e.name
				textToPrint += '\nqty:'+ e.qty
			})
			console.log(textToPrint)
		},
		printDiv(divName) {
			var textToPrint = this.$refs.cliente.clienteName
			var toPrint = document.createElement('p')	
			var originalContents = document.body.innerHTML;

			this.items.forEach(function(e){
				textToPrint +="\n-------------"
				textToPrint += '\nproduto:'+ e.name
				textToPrint += '\nqty:'+ e.qty
			})

			document.body.innerHTML = toPrint;
			window.print();
			document.body.innerHTML = originalContents;
		},
		stock(prod_qty){
			var products = prod_qty.split(',')
			var productsQty = []

			products.forEach(function (prodQ) {
				if(prodQ !== ''){
					prodQ = prodQ.split('|')
					productsQty.push({id: prodQ[0], qty: prodQ[1]})
				}
			})
			const self = this
			productsQty.forEach(function (prodQ) {
				var params = {
					action: 'find',
					from: 'produtos',
					where: `id|${prodQ.id}`
				}
				let oldQty = 0
				Promise.resolve(api.getEntityList(params))
					.then(function (results) {
						oldQty = results.data.msg[0].qty;
						params = {
							action: 'update',
							from: 'produtos',
							qty: parseInt(oldQty) - parseInt(prodQ.qty),
							where: `id|${prodQ.id}`
						}
						Promise.resolve(api.saveEntity(params))
							.then(function(resp){
								if(resp.data.msg == true){
									alert("pedido criado! :)")
									self.toPage('pedidosList')
								}
							})
						console.log(oldQty)
					});


			})
		},
		validateStock(item){
		},
		toPage(e){
			this.$parent.toPage(e)
		},
		addItem(item){
			let isNew = true
			const self = this
			this.total += helper.numberFormat(item.total)
			this.items.forEach(function (itemIn, key){
				if(itemIn.id == item.id){
					isNew = false
					self.items[key].qty += item.qty
					self.items[key].total = helper.numberFormat(self.items[key].total) + helper.numberFormat(item.total)
					self.items[key].total = helper.moneyFormat(self.items[key].total)
					return
				}
			})
			if(isNew){
				this.items.push(item)
				this.cont = this.items.length
			}
		}
	}
}
</script>

