<template>
	<div class="container">
		<div class="row">
			<div class="column" >
				<h4>Saída #{{ entity_id }}</h4>
			</div>
		</div>
		<form @submit.prevent="processForm($event)" id="saidas_form">
			<div class="row">
				<div class=" columns">
					<label for="description">Descrição</label>
					<textarea v-model="description" disabled class="u-full-width" placeholder="Descrição da saída..." id="description"></textarea>
				</div>
			</div>
			<div class="row">
				<div class="six columns">
					<label for="value">Valor</label>
					<p>{{ value | moneyFormat }}</p>
				</div>
			</div>
			<input hidden id="btn-save" type="button" value="Arquivada" disabled>
		</form>
	</div>
</template>

<script>
import api from '../../data/api'
import helper from '../../data/helper'

export default {
	name: 'saidasView',
	data() {
		return {
			id: 0,
			description: '',
			value: '',
		}
	},
	props: {
		entity_id: String
	},
	filters: {
		moneyFormat(val){
			return helper.moneyFormat(val)
		},
	},
	methods: {
		toPage(e){
			this.$parent.toPage(e)
		},
		processForm: function(){
			var params = {
				action: 'update',
				from: 'description',
				description: this.description,
				value: this.value,
				where: `id|${this.entity_id}`
			}

			const self = this;
			Promise.resolve(api.saveEntity(params))
				.then(function (results) {
					const resp = results;
					if(resp.data.msg == true){
						alert("saída atualizada! :)")
						self.toPage('saidaList')
					}
				});
		}
	},
	mounted(){
		var params = {
			from: 'saidas',
			action: 'find',
			where: `id|${this.entity_id}`
		}

		console.log('saida')
		const self = this;
		Promise.resolve(api.getEntity(params))
			.then(function (results) {
				self.description = results.data.msg[0].description
				self.value = results.data.msg[0].value
			});
	}
}
</script>

