<template>
	<div class="row">
		<h4>Cliente</h4>
		<div class="row">
			<div class="five columns">
				<label for="id">Id</label>
				<input v-model="clienteId" class="u-full-width" type="number" disabled placeholder="0" id="cliente-id">
			</div>
			<div class="five columns">
				<label for="name">Nome</label>
				<input  v-model="clienteName" 
						@keydown="getClienteComplete($event)"
						autocomplete="off"
						class="u-full-width" type="text" placeholder="Nome" id="cliente-name">
				<div>
					<ul  id="cliente-complet">
						<div v-for="cliente in clienteComplete" :key="cliente.id" >
							<li @click="select(cliente.id, cliente.name)">{{ cliente.name }}</li>
						</div>
					</ul>
				</div>
			</div>
			<div class="two columns">
				<button class="button" @click="novoCliente('clientesNew')">Novo</button>
			</div>
		</div>
	</div>
</template>

<script>
import api from '../../../data/api.js'

export default {
	name: 'Cliente',
	data() {
		return {
			clienteComplete: [],
			clienteName: '',
			clienteId: 0
		}
	},
	methods: {
		novoCliente(e){
			this.$parent.toPage(e)
		},
		select(id, name){
			this.clienteId = id
			this.clienteName = name
			this.clienteComplete = []
		},
		getClienteComplete(e){
			if(e.target.value.length > 1){
				var params = {
					action: 'find',
					from: 'clientes',
					where: `name|'%${e.target.value}%'-like`
				}
				const self = this;
				Promise.resolve(api.getEntityList(params))
					.then(function (results) {
						const resp = results.data;
						self.clienteComplete = resp.msg
					});
			}else{
				this.clienteComplete = []
			}
		}
	}
}
</script>

