<template>
	<div class="container">
		<div class="row">
			<div class="column" >
				<h4>Novo Produto</h4>
			</div>
		</div>
		<form @submit.prevent="processForm($event)" id="product_form">
			<div v-for="error in errors" :key="error.field">
				<p class="error">**{{ error.msg }}</p>
			</div>
			<div class="row">
				<div class="four columns">
					<label for="name">Nome</label>
					<input v-model="name" class="u-full-width" type="text" placeholder="Nome" id="name">
				</div>
				<div class="four columns">
					<label for="name">Valor</label>
					<input hidden v-model="value" class="u-full-width" type="text" id="value">
					<input  v-model="moneyFormat"
							@blur="focusOut" @focus="focusIn" @change="updateValue"
							class="u-full-width" type="text" placeholder="R$0,00" id="moneyFormat">
				</div>
				<div class="four columns">
					<label for="qty">Estoque</label>
					<input v-model="qty" class="u-full-width" type="number" placeholder="0" id="qty">
				</div>
			</div>
			<label for="description">Descrição</label>
			<textarea v-model="description" class="u-full-width" placeholder="Descrição do produto..." id="description"></textarea>
			<label for="recipe">Receita</label>
			<textarea v-model="recipe" class="u-full-width" placeholder="Receita do produto..." id="recipe"></textarea>
			<div class="row">
				<div class="six columns">
					<input class="button u-pull-left" type="button" value="Limpar">
				</div>
				<div class="six columns">
					<input id="btn-save" class="u-pull-right button-primary" type="submit" value="Salvar">
				</div>
			</div>
		</form>
	</div>
</template>

<script>
import api from '../../data/api.js'
import helper from '../../data/helper'

export default {
	name: 'produtosNew',
	data() {
		return {
			moneyFormat: 'R$ 0,00',
			name: '',
			value: '',
			qty: 0,
			description: '',
			errors: [],
			recipe: ''
		}
	},
	methods: {
		toPage(e){
			this.$parent.toPage(e)
		},
		focusIn(e){
			this.moneyFormat = helper.moneyFormat(e.target.value)
		},
		focusOut(e){
			this.moneyFormat = helper.moneyFormat(e.target.value)
		},
		updateValue(e){
			this.value = helper.numberFormat(e.target.value)
		},
		processForm(){
			var params = {
				action: 'create',
				from: 'produtos',
				name: {value:this.name, rule:'string'},
				description: {value: this.description, rule: 'string'},
				value: {value: this.value, rule: 'number'},
				qty: {value:this.qty, rule:'string'},
				recipe: this.recipe,
			}
			params = helper.chckeEmpytFields(params)
			if(params.error){
				this.errors = params.error
				return
			}
			const self = this;
			Promise.resolve(api.saveEntity(params, 'produto'))
				.then(function (results) {
					const resp = results;
					if(resp.data.msg == true){
						alert("produto cadastrado! :)")
						self.toPage('produtosList')
					}
				});
		}
	},
	mounted: function(){
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.error{
	color:red;
}
</style>
