<template>
	<div class="container">
		<div class="row">
			<div class="column" >
				<h1>Clientes</h1>
				<div>
					<button style="margin-right: 1%" @click="toPage('clientesNew')" class="button">Novo Cliente</button>
				</div>
				<div>
					<hr>
				</div>
				<div class="row">
					<label class="" for="name">Pesquisar:</label>
					<input class="u-full-width" type="text" placeholder="Nome" id="search" @keydown="search($event)">
				</div>
				<div>
					<table class="u-full-width">
						<thead>
							<tr>
								<th>Id</th>
								<th>Nome</th>
								<th>Cep</th>
								<th>Data</th>
								<th>Acão</th>
							</tr>
						</thead>
						<tbody v-if="haveEntities">
							<tr v-for="entity in entities" :key="entity.id">
								<td>{{ entity.id }}</td>
								<td>{{ entity.name }}</td>
								<td>{{ entity.cep }}</td>
								<td>{{ entity.created_at }}</td>
								<td>
									<a @click="toPage('clientesView', entity.id)" >editar</a>
									<span> |  </span>
									<a @click="clean('clientes', entity.id)" >deletar</a>
								</td>
							</tr>
						</tbody>
						<div v-if="!haveEntities">
							<p>Sem resgistros...</p>
						</div>
					</table>
					<div>
						<button class="button" href="entradas.php"> - </button>
						<span>1 </span>
						<button class="button" href="entradas.php"> - </button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import api from '../../data/api.js'

export default {
	name: 'clientesList',
	data() {
		return {
			haveEntities: false,
			entity:{
				name: '',
				cpf: '',
				cep: '',
				street: '',
				number: '',
				neighborhood: '',
				complement: '',
				created_at: ''
			},
			entities: []
		}
	},
	methods: {
		clean(e, id){
			var clean = window.confirm('Deseja realmente deletar?')
			if(clean){
				var params = {
					action: 'clean',
					from: e,
					where: `id|'${id}'`
				}
				const self = this
				Promise.resolve(api.getEntityList(params))
					.then(function (results) {
						const resp = results.data;
						if(resp.msg === true){
							self.getList()
						}
							
					});
			}
		},
		search(e){
			if(e.target.value.length > 1){
				var params = {
					action: 'find',
					from: 'clientes',
					where: `name|'%${e.target.value}%'-like`
				}
				const self = this;
				Promise.resolve(api.getEntityList(params))
					.then(function (results) {
						const resp = results.data;
						self.entities = resp.msg
					});
			}else{
				this.clienteComplete = []
			}
		},
		toPage(e, id){
			this.$parent.toPage(e, id)
		},
		getList: function(){
			var params = {
				action: 'findAll',
				from: 'clientes',
			}
			const self = this;
			Promise.resolve(api.getEntityList(params))
				.then(function (results) {
					const resp = results;
					if(resp.data.msg){
						self.haveEntities = true
						self.entities = resp.data.msg
					}
				});
		}
	},
	mounted: function() {
		this.getList()
	}
}
</script>

