import api from './api'

var helper = {

	validateStock() {},

	moneyFormat(number) {
		number = number.toString()
		//todo validar se tem 2d depois da ,
		number = number.replace('R$ ', '')
		number = number.replace('.', '')
		number = number.replace(',', '')
		var cents = number.substr(number.length - 2, 2)
		var money = number.substr(0, number.length - 2)
		return `R$ ${money},${cents}`
	},

	numberFormat(number) {
		number = number.toString()
		number = number.replace('R$', '')
		number = number.replace('.', '')
		number = number.replace(',', '')
		return parseInt(number)
	},

	chckeEmpytFields(fields) {
		var error = []
		Object.entries(fields).forEach(function (field) {
			if (field[1].rule) {
				if (field[1].rule === 'string') {
					if (field[1].value === null || field[1].value === '') {
						if (error.length < 1)
							error.push({msg: 'alguns campos estão sem valor', field: field[0]})
					}
					fields[field[0]] = field[1].value
				}
				else if (field[1].rule === 'number') {
					if (field[1].value === null || field[1].value === '' || isNaN(field[1].value)) {
						if (error.length < 1)
							error.push({msg: 'alguns campos estão sem valor', field: field[0]})
					}
					fields[field[0]] = field[1].value
				}
			}
		})
		if (error.length > 0)
			fields.error = error

		return fields
	}
}
export default helper
