<template>
	<div class="container">
		<div class="row">
			<div class="column" >
				<h1>Caixa</h1>
				<div>
					<p>Total em caixa: <span>{{ totalEntradas - totalSaidas | moneyFormat }}</span></p>
				</div>
				<div>
					<h4>Últimas Entradas</h4>
					<table class="u-full-width">
						<thead>
							<tr>
								<th>Id</th>
								<th>Cliente</th>
								<th>Total</th>
								<th>Data</th>
								<th>Acão</th>
							</tr>
						</thead>
						<tbody v-if="haveEntradas">
							<tr v-for="entity in entradas" :key="entity.id">
								<td>{{ entity.id }}</td>
								<td>{{ entity.cliente_name }}</td>
								<td>{{ entity.total | moneyFormat}}</td>
								<td>{{ entity.created_at }}</td>
								<td>
									<a @click="toPage('pedidosView', entity.id)" >visualizar</a>
								</td>
							</tr>
						</tbody>
						<div v-if="!haveEntradas">
							<p>Sem resgistros...</p>
						</div>
					</table>
					<div>
						<p>Total: <span>{{ totalEntradas | moneyFormat }}</span></p>
					</div>
					<div>
						<a @click="toPage('pedidosList')" >Todas as Entradas</a>
					</div>
				</div>

				<div>
					<hr>
				</div>

				<div>
					<h4>Últimas Saídas</h4>
					<table class="u-full-width">
						<thead>
							<tr>
								<th>Id</th>
								<th>Descrição</th>
								<th>Total</th>
								<th>Data</th>
								<th>Acão</th>
							</tr>
						</thead>
						<tbody v-if="haveSaidas">
							<tr v-for="entity in saidas" :key="entity.id">
								<td>{{ entity.id }}</td>
								<td>{{ entity.description }}</td>
								<td>{{ entity.value | moneyFormat}}</td>
								<td>{{ entity.created_at }}</td>
								<td>
									<a @click="toPage('saidasView', entity.id)" >visualizar</a>
								</td>
							</tr>
						</tbody>
						<div v-if="!haveSaidas">
							<p>Sem resgistros...</p>
						</div>
					</table>
					<div>
						<p>Total: <span>{{ totalSaidas | moneyFormat }}</span></p>
					</div>
					<div>
						<a @click="toPage('saidasList')" >Todas as Saídas</a>
					</div>
				</div>
				<hr />
			</div>
		</div>
	</div>
</template>

<script>
import api from '../../data/api.js'
import helper from '../../data/helper.js'

export default {
	name: 'caixaView',
	data(){
		return {
			haveSaidas: false,
			haveEntradas: false,
			entradas: [],
			saidas: [],
			totalEntradas: 0,
			totalSaidas: 0,
		}
	},
	filters: {
		moneyFormat(val){
			return helper.moneyFormat(val)
		},
	},
	methods: {
		toPage(e, id){
			this.$parent.toPage(e, id)
		},
		getEntradas(){
			var params = {
				action: 'findAll',
				from: 'pedidos',
			}
			const self = this;
			Promise.resolve(api.getEntityList(params))
				.then(function (results) {
					const resp = results;
					if(resp.data.msg){
						self.haveEntradas = true
						self.entradas = resp.data.msg
						self.entradas.forEach(function(entrada){
							self.totalEntradas += helper.numberFormat(entrada.total)
						})
					}
				});
		},
		getSaidas(){
			var params = {
				action: 'findAll',
				from: 'saidas',
			}
			const self = this;
			Promise.resolve(api.getEntityList(params))
				.then(function (results) {
					const resp = results;
					if(resp.data.msg){
						self.haveSaidas= true
						self.saidas = resp.data.msg
						self.saidas.forEach(function(saida){
							self.totalSaidas += helper.numberFormat(saida.value)
						})
					}
				});
		},
	},
	mounted(){
		this.getEntradas()
		this.getSaidas()
	}
}
</script>

