<template>
	<div class="container">
		<div class="row">
			<div class="column" >
				<h1>Pedidos</h1>
				<div>
					<button style="margin-right: 1%" @click="toPage('pedidosNew')" class="button">Novo Pedido</button>
				</div>
				<div>
					<hr>
				</div>
				<div class="row">
					<label class="" for="name">Pesquisar:</label>
					<input class="u-full-width" type="text" placeholder="Cliente" id="search" @keydown="search($event)">
				</div>
				<div>
					<table class="u-full-width">
						<thead>
							<tr>
								<th>Id</th>
								<th>Cliente</th>
								<th>Total</th>
								<th>Data</th>
								<th>Acão</th>
							</tr>
						</thead>
						<tbody v-if="haveEntities">
							<tr v-for="entity in entities" :key="entity.id">
								<td>{{ entity.id }}</td>
								<td>{{ entity.cliente_name }}</td>
								<td>{{ entity.total | moneyFormat}}</td>
								<td>{{ entity.created_at }}</td>
								<td>
									<a @click="toPage('pedidosView', entity.id)" >visualizar</a>
									<span> |  </span>
									<a @click="clean('pedidos', entity.id)" >deletar</a>
								</td>
							</tr>
						</tbody>
						<div v-if="!haveEntities">
							<p>Sem resgistros...</p>
						</div>
					</table>
					<div>
						<button class="button" href="entradas.php"> - </button>
						<span>1 </span>
						<button class="button" href="entradas.php"> - </button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import api from '../../data/api.js'
import helper from '../../data/helper.js'

export default {
	name: 'pedidosList',
	data(){
		return {
			haveEntities: false,
			entities: []
		}
	},
	filters: {
		moneyFormat(val){
			return helper.moneyFormat(val)
		},
	},
	methods: {
		clean(e, id){
			var clean = window.confirm('Deseja realmente deletar?')
			if(clean){
				var params = {
					action: 'clean',
					from: e,
					where: `id|'${id}'`
				}
				const self = this
				Promise.resolve(api.getEntityList(params))
					.then(function (results) {
						const resp = results.data;
						if(resp.msg === true){
							self.getList()
						}
							
					});
			}
		},
		search(e){
			if(e.target.value.length > 1){
				var params = {
					action: 'find',
					from: 'pedidos',
					where: `cliente_name|'%${e.target.value}%'-like`
				}
				const self = this;
				Promise.resolve(api.getEntityList(params))
					.then(function (results) {
						const resp = results.data;
						self.entities = resp.msg
					});
			}else{
				this.clienteComplete = []
			}
		},
		getList(){
			var params = {
				action: 'findAll',
				from: 'pedidos',
			}
			const self = this;
			Promise.resolve(api.getEntityList(params))
				.then(function (results) {
					const resp = results;
					if(resp.data.msg){
						self.haveEntities = true
						self.entities = resp.data.msg
					}
				});
		},
		toPage(e, id){
			this.$parent.toPage(e, id)
		},
	},
	mounted(){
		this.getList()
	}
}
</script>

