<template>
	<div class="container">
		<div class="row">
			<div class="column" >
				<h1>Saídas</h1>
				<div>
					<button style="margin-right: 1%" @click="toPage('saidasNew')" class="button">Nova Saída</button>
				</div>
				<div>
					<hr>
				</div>
				<div class="row">
					<label class="" for="name">Pesquisar:</label>
					<input class="u-full-width" type="text" placeholder="Descrição..." id="search" @keydown="search($event)">
				</div>
				<div>
					<table class="u-full-width">
						<thead>
							<tr>
								<th>Id</th>
								<th>Descrição</th>
								<th>Total</th>
								<th>Data</th>
								<th>Acão</th>
							</tr>
						</thead>
						<tbody v-if="haveEntities">
							<tr v-for="entity in entities" :key="entity.id">
								<td>{{ entity.id }}</td>
								<td>{{ entity.description }}</td>
								<td>{{ entity.value | moneyFormat }}</td>
								<td>{{ entity.created_at }}</td>
								<td>
									<a @click="toPage('saidasView', entity.id)" >visualizar</a>
								</td>
							</tr>
						</tbody>
						<div v-if="!haveEntities">
							<p>Sem resgistros...</p>
						</div>
					</table>
					<div>
						<button class="button" href="entradas.php"> - </button>
						<span>1 </span>
						<button class="button" href="entradas.php"> - </button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import api from '../../data/api'
import helper from '../../data/helper'

export default {
	name: 'saidasList',
	data() {
		return {
			haveEntities: false,
			entity:{
				description: '',
				value: '',
				created_at: ''
			},
			entities: []
		}
	},
	filters: {
		moneyFormat(val){
			return helper.moneyFormat(val)
		},
	},
	methods: {
		search(e){
			if(e.target.value.length > 1){
				var params = {
					action: 'find',
					from: 'saidas',
					where: `description|'%${e.target.value}%'-like`
				}
				const self = this;
				Promise.resolve(api.getEntityList(params))
					.then(function (results) {
						const resp = results.data;
						self.entities = resp.msg
					});
			}else{
				this.clienteComplete = []
			}
		},
		toPage(e, id){
			this.$parent.toPage(e, id)
		},
		getList: function(){
			var params = {
				action: 'findAll',
				from: 'saidas',
			}
			const self = this;
			Promise.resolve(api.getEntityList(params))
				.then(function (results) {
					const resp = results;
					if(resp.data.msg){
						self.haveEntities = true
						self.entities = resp.data.msg
					}
				});
		}
	},
	mounted() {
		this.getList()
	}
}
</script>

