<template>
	<div class="row">
		<h4>Produto</h4>
		<div>
			<div class="eleven columns">
				<div class="three columns">
					<label for="id">Id</label>
					<input disabled v-model="id" class="u-full-width not-data" type="number" placeholder="0" id="id">
				</div>
				<div class="three columns">
					<label for="name">Nome</label>
					<input  v-model="name" 
							@keydown="getProductComplete($event)"
							autocomplete="off"
							class="u-full-width not-data" type="text" placeholder="Nome" id="name">
					<div>
						<ul id="product-complet">
							<div v-for="product in produtoComplete" :key="product.id" >
								<li @click="select(product)">{{ product.name }}</li>
							</div>
						</ul>
					</div>
				</div>
				<div class="three columns">
					<label for="name">Valor</label>
					<input hidden v-model="value" class="u-full-width" type="text" id="value">
					<input  v-model="moneyFormat"
							@blur="focusOut" @focus="focusIn" @change="updateValue"
							class="u-full-width" type="text" placeholder="R$0,00" id="moneyFormat">
				</div>
				<div class="three columns">
					<label for="name">Quantidade</label>
					<input v-model="qty" class="u-full-width not-data" type="text" placeholder="Nome" id="qty">
				</div>
			</div>
			<div class="one columns">
				<button  @click="addProduct($event)" type="button"  class="button" >Adicionar</button>
			</div>
		</div>
	</div>
</template>

<script>
import helper from '../../../data/helper.js'
import api from '../../../data/api.js'

export default {
	name: 'AddProduto',
	data(){
		return {
			produtoComplete: [],
			name: '',
			moneyFormat: 'R$ 0,00',
			id: 0,
			value: 0,
			qty: 1
		}
	},
	methods: {
		addProduct(){
			var total = helper.numberFormat(this.moneyFormat) * parseInt(this.qty)
			total = helper.moneyFormat(total.toString())
			this.$emit('additem', 
				{name:this.name, id:this.id, value:this.value, qty:parseInt(this.qty), total: total}
			)
		},
		select(product){
			this.id = product.id
			this.value = product.value
			this.name = product.name
			this.moneyFormat = helper.moneyFormat(product.value)
			
			this.produtoComplete = []
		},
		focusIn(e){
			this.moneyFormat = helper.moneyFormat(e.target.value)
		},
		focusOut(e){
			this.moneyFormat = helper.moneyFormat(e.target.value)
		},
		updateValue(e){
			this.value = helper.numberFormat(e.target.value)
		},
		getProductComplete(e){
			if(e.target.value.length > 1){
				var params = {
					action: 'find',
					from: 'produtos',
					where: `name|'%${e.target.value}%'-like`
				}
				const self = this;
				Promise.resolve(api.getEntityList(params))
					.then(function (results) {
						const resp = results.data;
						self.produtoComplete = resp.msg
					});
			}else{
				this.clienteComplete = []
			}
		}

	}

}
</script>

