<template>
	<div class="container">
		<div class="row">
			<div class="column" >
				<h4>Produto #{{ entity_id }}</h4>
			</div>
			<form @submit.prevent="processForm($event)" id="product_form">
				<!--<div class="row">-->
				<!--<div class="six columns">-->
				<!--<input class="u-full-width" type="file" placeholder="0" id="image">-->
				<!--</div>-->
				<!--</div>-->
				<div class="row">
					<div class="four columns">
						<label for="name">Nome</label>
						<input  v-model="name" class="u-full-width" type="text" placeholder="Nome" id="name">
					</div>
					<div class="four columns">
						<label for="name">Valor</label>
						<input v-model="value" class="u-full-width" type="text" placeholder="R$00,00" id="value">
					</div>
					<div class="four columns">
						<label for="qty">Estoque</label>
						<input v-model="qty" class="u-full-width" type="number" placeholder="0" id="qty">
					</div>
				</div>
				<label for="description">Descrição</label>
				<textarea v-model="description" class="u-full-width" placeholder="Descrição do produto..." id="description"></textarea>
				<label for="recipe">Receita</label>
				<textarea v-model="recipe" class="u-full-width" placeholder="Receita do produto..." id="recipe"></textarea>
				<div class="row">
					<div class="six columns">
						<input class="button u-pull-left" type="button" value="Limpar">
					</div>
					<div class="six columns">
						<input id="btn-save" class="u-pull-right button-primary" type="submit" value="Salvar">
					</div>
				</div>
			</form>
		</div>
	</div>
</template>

<script>
import api from '../../data/api'

export default {
	name: 'produtosView',
	data() {
		return {
			id: 0,
			name: '',
			value: '',
			qty: 0,
			description: '',
			recipe: ''
		}
	},
	props: {
		entity_id: String
	},
	methods: {
		toPage(e){
			this.$parent.toPage(e)
		},
		processForm: function(){
			var params = {
				action: 'update',
				from: 'produtos',
				name: this.name,
				value: this.value,
				qty: this.qty,
				description: this.description,
				recipe: this.recipe,
				where: `id|${this.entity_id}`
			}

			const self = this;
			Promise.resolve(api.saveEntity(params, 'produto'))
				.then(function (results) {
					const resp = results;
					if(resp.data.msg == true){
						alert("produto atualizado! :)")
						self.toPage('produtosList')
					}
				});
		}
	},
	created: function(){
		var params = {
			from: 'produtos',
			action: 'find',
			where: `id|${this.entity_id}`
		}

		const self = this;
		Promise.resolve(api.getEntity(params))
			.then(function (results) {
				self.name = results.data.msg[0].name
				self.value = results.data.msg[0].value
				self.description = results.data.msg[0].description
				self.recipe = results.data.msg[0].recipe
				self.qty = results.data.msg[0].qty
			});
	}
}
</script>

