<template>
	<div class="container">
		<div class="row">
			<div class="one-half column" >
				<h4>Cliente #{{ entity_id }}</h4>
			</div>
		</div>
		<form @submit.prevent="processForm($event)" id="clientes_form">
			<div class="row">
				<div class="six columns">
					<label for="name">Nome</label>
					<input v-model="name" class="u-full-width" type="text" placeholder="Nome" id="name">
				</div>
				<div class="six columns">
					<label for="cpf">Cpf</label>
					<input v-model="cpf" class="u-full-width" type="text" placeholder="000.000.000-00" id="cpf">
				</div>
			</div>
			<hr>
			<div class="row">
				<div class="six columns">
					<label for="cep">Cep</label>
					<input v-model="cep" class="u-full-width" type="text" placeholder="00000-000" id="cep">
				</div>
			</div>
			<div class="row">
				<div class="five columns">
					<label for="street">Rua</label>
					<input v-model="street" class="u-full-width" type="text" placeholder="0" id="street">
				</div>
				<div class="two columns">
					<label for="number">Numero</label>
					<input v-model="number" class="u-full-width" type="text" placeholder="0" id="number">
				</div>
				<div class="five columns">
					<label for="neighborhood">Bairro</label>
					<input v-model="neighborhood" class="u-full-width" type="text" placeholder="0" id="neighborhood">
				</div>
			</div>
			<div class="row">
				<div class="six columns">
					<label for="complement">Complemento</label>
					<input v-model="complement" class="u-full-width" type="text" placeholder="0" id="complement">
				</div>
			</div>
			<div class="row">
				<div class="six columns">
					<input class="button u-pull-left" type="button" value="Limpar">
				</div>
				<div class="six columns">
					<input id="btn-save" class="u-pull-right button-primary" type="submit" value="Salvar">
				</div>
			</div>
		</form>
	</div>

</template>

<script>
import api from '../../data/api.js'

export default {
	name: 'clientesView',
	data() {
		return {
			name: '',
			cpf: '',
			cep: '',
			street: '',
			number: '',
			neighborhood: '',
			complement: ''
		}
	},
	props: {
		entity_id: String
	},
	methods: {
		toPage(e){
			this.$parent.toPage(e)
		},
		processForm: function(){
			var params = {
				action: 'create',
				from: 'clientes',
				name: this.name,
				cpf: this.cpf,
				cep: this.cep,
				street: this.street,
				number: this.number,
				neighborhood: this.neighborhood,
				complement: this.complement,
				where: `id|${this.entity_id}`
			}

			const self = this;
			Promise.resolve(api.saveEntity(params, 'clientes'))
				.then(function (results) {
					const resp = results;
					if(resp.data.msg == true){
						alert("cliente atualizado! :)")
						self.toPage('clientesList')
					}
				});
		}
	},
	created: function(){
		var params = {
			from: 'clientes',
			action: 'find',
			where: `id|${this.entity_id}`
		}

		const self = this;
		Promise.resolve(api.getEntity(params))
			.then(function (results) {
				self.name = results.data.msg[0].name
				self.cpf = results.data.msg[0].cpf
				self.cep = results.data.msg[0].cep
				self.street = results.data.msg[0].street
				self.number = results.data.msg[0].number
				self.neighborhood = results.data.msg[0].neighborhood
				self.complement = results.data.msg[0].complement
			});
	}
}
</script>

